.imgAbout {
  width: 100vw;
  height: 550px;
  object-fit: cover;
  object-position: 20% 30%;
}

.emoji {
  width: 75px;
  height: 70px;
}

@media screen and (max-width: 750px) {
  .imgAbout {
    width: 500px;
    height: 300px;
    object-fit: scale-down;
    object-position: 50% 50%;
  }
}

@media screen and (max-width: 750px) {
  .emojis {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  }
}


@media screen and (max-width: 750px) {
  .emoji {
    margin: 20px;
  }
}


@media screen and (max-width: 750px) {
  .name {
    font-size: 1rem;
  }
}

@media screen and (max-width: 750px) {
  .hobby {
    font-size: 0.8rem;
  }
}